<!-- 正在使用 -->
<template>
    <div class="mask" v-if="isShow">
        <div class="program-box">
            <div class="program-title-line">
                <span class="program-title-msg">{{ $t('newpro.newpro') }}</span>
                <span class="program-box-close" @click="cancel">
                    <svgloader class="icon-close" icon="Close" size="1.2rem"></svgloader>
                </span>
            </div>

            <div class="program-pset-line">
                <div class="program-copy-text">{{$t('newpro.jobId')}}</div>
                <div class="program-pset-input">
                    <Counter _width="100%" :cnum="newJobId.text" :_min="1" :_max="999999999" @update='updateNewJob($event)'
                        _ftszie="2rem" :textValid="checkJob()" validReason="$t('newpro.repeatJob')"></Counter>
                </div>
            </div>
            <div class="program-mode-line">
                <div class="program-copy-text">{{$t('newpro.jobName')}}</div>
                <InputText :text="newJobName.text" :placeholder="$t('newpro.inputJobName')" class="program-name-input" v-model="newJobName"
                    :textValid="checkJobName()" :validReason="nameError"></InputText>
            </div>
            <div class="program-name-line">
                <div class="program-copy-text">{{$t('newpro.des')}}</div>
                <InputText :text="newJobDesc.text" :placeholder="$t('newpro.inputJobDes')" class="program-name-input" v-model="newJobDesc">
                </InputText>
            </div>


            <div class="program-operate-line">
                <div class="program-cancel-btn" @click="cancel">{{ $t('newpro.cancel') }}</div>
                <div class="program-cnfrm-btn" @click="sbmtNewPrgrmData">{{ $t('newpro.confirm') }}</div>
            </div>
        </div>
    </div>
</template>

<script>

import InputText from "@/components/input/InputText.vue"
import svgloader from '@/components/icon_font/svgloader.vue'
import Counter from '@/components/controls/Counter.vue'
// import { deepCopy } from '@/utils/deepCopy'
// import getEmptyProfile from '@/utils/empty_profile'
import { genJobProfile } from '@/components/profiles/profileHelper'

export default {
    components: {
        svgloader, InputText, Counter,
    },
    data() {
        return {
            // title:"新建程序",
            isShow: true,
            // 选择的模式
            curMode: 2,
            // 当前文件
            Profile: undefined,
            box: undefined,
            newJobName: {
                'text': "",
                "valid": false
            },
            newJobId: {
                'text': 1,
                "valid": false
            },
            newJobDesc: {
                'text': "",
                "valid": false
            },


        }
    },
    props: {
        // 可选的模式
        // 文件列表
        pfList: {
            type: Array,
            default: function () {
                return []
            }
        },
    },
    methods: {
        // 确认按钮
        sbmtNewPrgrmData() {
            if (!this.checkJobName() || !this.checkJob()) { return }
            this.isShow = false
            this.Profile = genJobProfile(this.curMode);
            this.Profile.Name = this.newJobName.text
            this.Profile.JobId = this.newJobId.text
            this.Profile.Descr = this.newJobDesc.text
            let job = this.newJobId.text
            this.onConfirm && this.onConfirm({ 'Profile': { 'Profile': this.Profile }, job })
        },
        // 取消新建文件
        cancel() {
            this.isShow = false
            let msg = this.$t('dlgprogram.userCancel')
            this.onCancel && this.onCancel(msg)
        },

        // 关闭添加工艺的对话框
        closeboard(e) {
            let x = e.clientX
            let y = e.clientY
            let that = this
            let left = that.box.offsetLeft - that.box.offsetWidth / 2
            let right = that.box.offsetLeft + that.box.offsetWidth - that.box.offsetWidth / 2
            let top = that.box.offsetTop
            let btm = that.box.offsetTop + that.box.offsetHeight
            if ((x * y != 0) && (x <= left || x >= right || y >= btm || y <= top)) {
                window.removeEventListener('click', this.closeboard)
                that.isShow = false
            }
        },
        getBox() {
            this.box = document.getElementsByClassName("program-box")[0]
        },

        // 用户手动更新job
        updateNewJob(job) {
            this.newJobId.text = job
        },
        // 检测文件名是否合法
        checkJobName() {
            for (let item of this.pfList) {
                if ( item.name === this.newJobName.text) {
                    return false
                }
            }
            return true
        },

        // 检测文件pset是否合法
        checkJob() {
            for (let item of this.pfList) {
                if (item.job === this.newJobId.text || this.newJobId.text === "" || this.newJobId.text>255) {
                    return false
                }
            }
            return true
        },
        // 初始化一个可用文件
        initDefaultProfile() {
            this.setJobId()
            this.setJobName()
        },
        setJobId(){
            if (this.checkJob()) return
            this.newJobId.text = 1
            for (let i in this.pfList) {
                if (this.pfList[i].job !== this.newJobId.text) {
                    break
                }
                this.newJobId.text = this.newJobId.text + 1
            }
        },
        // 更新文件名
        setJobName() {
            if(this.checkJobName()==false) return
            this.newJobName.text = `job${this.newJobId.text}`
        },

    },
    computed: {
        nameError() {
            if (this.newJobName.text == "") {
                return this.$t('dlgprogram.nameNotEmpty')
            }
            return this.$t('dlgprogram.nameRepeat')
        }
    },
    watch: {
        pfList: {
            immediate: true,
            deep: true,
            handler: function () {
                this.initDefaultProfile()
                // this.initNameWithJob()
            }
        },
    },
    mounted() {
        setTimeout(() => {
            this.getBox()
            window.addEventListener('click', this.closeboard)
        }, 50)
    },
}
</script>
