import axios_ins from "../../utils/axios_ins";



export function pfModJobApi(paras) {
    return axios_ins({
        url: "pf/jobmod",
        method: "post",
        data: paras
    })
}


export function curPfJobApi(id) {
    return axios_ins({
        url: `pf/cur/job?id=${id}`,
        method: "get",
    })
}

export function curPfJobListApi() {
    return axios_ins({
        url: "pf/cur/joblst",
        method: "get",
    })
}


export function pfJobDelApi(paras) {
    return axios_ins({
        url: "pf/jobdel",
        method: "post",
        params: paras
    })
}


export function pfCurJobSelApi(job) {
    return axios_ins({
        url: `pf/cur/jobsel?job=${job}`,
        method: "get",
    })
}
